.App {
  /* text-align: center; */
  background-color: #ffffff;
  /* box-sizing: border-box;
  margin: 0;
  padding: 0; */

  font-family: "Nunito", sans-serif;
}
/* Header picture */
.bg {
  height: 45vh;
  background-image: url(background.jpg);
  background-size: cover;
  background-position: 0% 70%;
}

/* Navbar */
nav {
  background-color: rgba(255, 255, 255, 0.616);
  width: 80%;
  height: 3.1em;
  position: sticky;
  top: -1px;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: -3.1em auto 0 auto;
  padding-left: 10%;
  padding-right: 10%;
}

.stickyBg {
  background-color: rgb(255, 255, 255);
  height: calc(3.1em + 1px);
}

.navLinks {
  list-style: none;
}

.navLinks li {
  display: inline-block;
  padding: 10px;
}

/* Grid*/
.grid-container {
  display: grid;
  grid-template-rows: 55vh 55vh 65vh;
  grid-template-columns: 100%;
}

.row-container {
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: 50% 50%;
}

.a {
  background-image: url(h1.jpg);
  /* background-size: contain; */
  background-size: cover;
  /* background-repeat: no-repeat; */
}

.b {
  background-image: url(h2.jpg);
  background-size: cover;
}

.c {
  background-image: url(h3.jpg);
  background-size: cover;
}

.column-left {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 20%;
  padding-right: 10%;
}

.a1 {
  background-color: #c2e595c5;
  /* align-items: center; */
}

.b1 {
  background-color: #eb76fd3a;
}

.c1 {
  background-color: #fff5a098;
  align-items: center;
  padding-left: 10%;
}

.column-right {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 20%;
  padding-right: 10%;
}

.a2 {
  /* background-color: #c2e595; */
  background-color: #ffffff44;
}

.b2 {
  background-color: #eb76fdcc;
  /* #e85dfde6 */
}

.c2 {
  background-color: #fff5a059;
}

/* components Square.js */
.square {
  height: 10px;
  width: 10px;
  margin-right: 1em;
  min-width: 10px;
  max-width: 10px;
  min-height: 10px;
  max-height: 10px;
}

.textRow {
  display: flex;
  justify-content: center;
  align-items: center;
  /* max-width: 55%; */
}

.button {
  margin-top: 1rem;
  background-color: #535353;
  text-align: center;
  border-radius: 0.2rem;
  border: #ea76fd 0.1rem solid;
  padding: 0.5rem 1.3rem;
  color: #bbcfa1;
  line-height: 1.1;
  min-width: 10ch;
  min-height: 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
}
a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration-line: underline;
  -webkit-text-decoration-color: #938f3e; /* Safari */
  text-decoration-color: #938f3e;
  /* text-decoration-thickness: 5px; miksi ei toimi */
}

/* a:active {
  text-decoration: underline;
  -webkit-text-decoration-color: #938f3e; 
  text-decoration-color: #938f3e;
} */

.footer {
  background-color: #fff5a098;
  width: 80%;
  height: 9.1em;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
}

.footerRow {
  line-height: 2px;
}

form {
  display: flex;
  align-items: center;
  flex-direction: column;
}

input {
  border: #bbcfa1 0.1rem solid;
  margin: 0px 0px 20px;
  padding: 10px 20px;
  width: 300px;
}

textarea {
  border: #bbcfa1 0.1rem solid;
  width: 300px;
  padding: 10px 20px;
}

button {
  margin-top: 1rem;
  background-color: #535353;
  text-align: center;
  border-radius: 0.2rem;
  border: #ea76fd 0.1rem solid;
  padding: 0.5rem 1.3rem;
  color: #bbcfa1;
  line-height: 1.1;
  min-width: 10ch;
  min-height: 35px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
}

/* Media query */

@media only screen and (max-width: 500px) {
  .navButtons {
    font-size: 10px;
  }
  ul {
    padding-inline-start: 0px;
    font-size: 10px;
  }

  .grid-container {
    display: grid;
    grid-template-rows: 65vh 55vh 85vh;
    grid-template-columns: 100%;
  }

  .row-container {
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 100%;
  }

  .column-left.b1 {
    display: none;
  }

  .a1 .button {
    margin-left: 1.5em;
  }

  input {
    margin: 0px 0px 5px;
    width: 250px;
  }

  textarea {
    width: 250px;
  }

  .footer {
    /* line-height: 0px; */
    font-size: 10px;
  }

  /* h1 {
    font-size: 1.5em;
  } */
}
